.follow {
  text-align: center;
  padding-bottom: 131px;

  @media (max-width: 1023px) {
    padding-bottom: 78px;
  }

  @media (max-width: 767px) {
    padding-bottom: 71px;
  }
}

.follow h2 {
  font-family: var(--font-secondary);
  font-size: 34px;

  @media (max-width: 1023px) {
    margin-bottom: 19px;
  }
}

.follow__photos img {
  height: 100%;

  @media (max-width: 767px) {
    width: 100%;
  }
}

.follow__photos :nth-child(7n+2) {
  grid-row: span 2;
}

.follow__photos {
  display: grid;
  grid-template-columns: 19.65% 34.1% 19.65% 19.65%;
  column-gap: 27px;
  row-gap: 34px;
  margin-top: 63px;

  @media (max-width: 1023px) {
    column-gap: 16px;
    row-gap: 21px;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr 1fr;
    column-gap: 24px;
    row-gap: 27px;
    margin-top: 48px;

    :nth-child(7n+3),
    :nth-child(7n+4) {
      display: none;
    }
  }
}
